.home-background{
    background-image: url('../../images/baku-home.jpg');
    background-position: center; 
    width: 100%;
    height: 100vh;
	background-color: rgb(255 255 255);
	background-size: contain;
background-repeat: no-repeat;
}
@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}
$animationSpeed: 40s;
// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}
// Styling
.slider {
	background: white;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 100px;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 100%;
	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100px;
		position: absolute;
		width: 40%;
		z-index: 2;
	}
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}
	&::before {
		left: 0;
		top: 0;
	}
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(250px * 14);
	}
	.slide {
		height: 100px;
		width: 250px;
	}
	img {
		width: 180px;
		margin: 0px 70px;
		padding: 10px;
	}
}



@media only screen and (max-width:1024px) {
	
	.home-background{
		height: 50vh;
	}
	.d-flex {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-around;
	}
}

@media only screen and (max-width:375px) {
	
	.home-background{
		height: 34.1vh;
		position: relative;
		top: 30px;
	}
}
@media only screen and (max-width:320px){
	.home-background {
		position: relative;
		height: 40vh;
		top: 30px;
	}
	.d-flex {
		display: flex;
		align-items: center;
		flex-direction: column;
	}
}