@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
}
:root {
	--mainColor: #29335c;
	--mainColorLight: #5767aa;
	--secondaryColor: #db2b39;
	--textColor: rgb(0, 0, 0);
	--mainColorWhite: rgb(255, 255, 255);
}

  
header {
	position: fixed;
	height: 70px;
	width: 100%;
	top: 0;
	padding: 10px 50px;
	background-color: var(--mainColorWhite);
	color: var(--textColor);
	z-index: 100000;
	box-shadow: 0px 10px 10px lightblue;
}
.container-nav{
	width: 100%;

}
header div,
nav {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
nav a {
	padding: 0px 10px;
	width: fit-content;
	font-size: 15px;
	color: var(--textColor);
	text-decoration: none;
	text-transform: uppercase;
}

nav a:hover {
	color: var(--secondaryColor);
}
.right {
	position: absolute;
	right: 50px;
}
.right a{
	padding-right: 30px;
}
.right i{
	margin-left: 40px;
	font-size: 20px;
}
.login{
	gap: 20px;
}
header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	width: fit-content;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}
.hover-underline-animation {
	display: inline-block;
	position: relative;
	color: #0087ca;
}
  
.hover-underline-animation::after {
	content: '';
	position: absolute;
	width: 100%;
	transform: scaleX(0);
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: #0087ca;
	transform-origin: bottom right;
	transition: transform 0.25s ease-out;
}
  
.hover-underline-animation:hover::after {
	transform: scaleX(1);
	transform-origin: bottom left;
}


@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;

	}
	header nav {
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		z-index: 100;
		background-color: var(--mainColor);
		transition: 1s;
		transform: translateY(-100vh);
		padding: 100px 0px 0px 0px ;
	}
	header{
		position:fixed;
	}
	header .responsive_nav {
		transform: none;
		padding: inherit;
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
	.right{
		display: none;
	}
}

