.d-flex {
    display: flex;
  }
  .align-center {
    align-items: center;
  }
  .flex-centerY-centerX {
    justify-content: center;
    align-items: center;
  }
  
  .page-wrapper {
    height: 100%;
    display: table;
  }
  
  /* .search{
    
  } */
  .sale{
    width: 75px;
    padding: 0px 10px;
    position: absolute;
    bottom: 0;
    font-size: 14px;
    letter-spacing: 10px;
    color: white;
    border-radius: 0px 10px 0px 0px;
    background-color: rgb(255, 0, 0);
  }
  
  .page-wrapper .page-inner {
    display: table-cell;
    vertical-align: middle;
  }
  .el-wrapper {
    width: 240px;
    padding: 10px;
    margin: 10px;
    background-color: #fff;
    border-radius: 10px;
  }
  
  .el-wrapper:hover .h-bg {
    left: 0px;
  }
  .el-wrapper:hover .price {
    left: 20px;
    transform: translateY(-50%);
    color: #818181;
  }
  .el-wrapper:hover .add-to-cart {
    left: 50%;
  }
  .el-wrapper:hover .img {
    filter: blur(5px);
    filter: progid:DXImageTransform.Microsoft.Blur(pixelradius='7', shadowopacity='0.0');
    opacity: 0.4;
  }
  .el-wrapper:hover .info-inner {
    bottom: 150px;
  }
  .el-wrapper:hover .a-size {
    -webkit-transition-delay: 300ms;
    -o-transition-delay: 300ms;
    transition-delay: 300ms;
    bottom: 50px;
    opacity: 1;
  }
  .el-wrapper .box-down {
    width: 100%;
    height: 60px;
    position: relative;
    overflow: hidden;
  }
  .el-wrapper .box-up {
    width: 100%;
    height: 250px;
    position: relative;
    overflow: hidden;
    text-align: center;
  }
  .el-wrapper .img {
    transition: all 800ms cubic-bezier(0, 0, 0.18, 1);
  }
  .h-bg {
    transition: all 800ms cubic-bezier(0, 0, 0.18, 1);
    width: 660px;
    height: 100%;
    background-color: skyblue;
    position: absolute;
    left: -659px;
  }
  .h-bg .h-bg-inner {
    width: 50%;
    height: 100%;
    background-color: #464646;
  }
  .info-inner {
    transition: all 400ms cubic-bezier(0, 0, 0.18, 1);
    position: absolute;
    width: 100%;
    bottom: 0px;
  }
  .info-inner .p-name,
  .info-inner .p-company {
    display: block;
  }
  .info-inner .p-name {
    font-family: 'PT Sans', sans-serif;
    font-size: 18px;
    color: #252525;
  }
  .info-inner .p-company {
    font-family: 'Lato', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    color: #8c8c8c;
  }
  .a-size {
    transition: all 300ms cubic-bezier(0, 0, 0.18, 1);
    position: absolute;
    width: 100%;
    bottom: -20px;
    font-family: 'PT Sans', sans-serif;
    color: #828282;
    opacity: 0;
  }
  
  .a-size .size {
    color: #252525;
  }
  
  .cart {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
  }
  
  .cart .price {
    transition: all 600ms cubic-bezier(0, 0, 0.18, 1);
    transition-delay: 100ms;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
    color: #252525;
  }
  
  .cart .add-to-cart {
    transition: all 600ms cubic-bezier(0, 0, 0.18, 1);
    transition-delay: 100ms;
    display: block;
    position: absolute;
    left: 100%;
    top: 10%;
  
  }
  
  .cart .add-to-cart .txt {
    font-size: 12px;
    color: #fff;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    white-space: nowrap;
  }
  .grid-container{
    display: flex;
    justify-content: center;
  }
  .grid-item {
    z-index: 20;
    padding: 20px;
    position:relative;
    font-size: 30px;
    text-align: center;
    position: revert;
    display: grid;
    grid-template-columns: auto auto auto auto;
  }
  .flex-column {
    flex-direction: column!important;
  }
