.contact{
	height: 100vh;
	padding:100px 0px 200px;
	background: rgb(119,119,255);
	background: linear-gradient(90deg, rgba(119,119,255,1) 0%, rgba(94,227,255,1) 50%, rgba(119,119,255,1) 100%);
}
.form {
	top: 100px;
	width: 50%;
	height: 440px;
	border-radius: 8px;
	background: black;
	color: white;
	box-shadow: 0 0 40px 0px #000;
	margin: 10px 25% ;
	padding: 20px 30px;
	box-sizing: border-box;
	font-family: "Montserrat", sans-serif;
	position: relative;
  }
 .form h2 {
	margin: 10px 0;
	padding-bottom: 10px;
	width: 180px;
  }
 .form input {
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	background: none;
	outline: none;
	resize: none;
	border: 0;
	color: white;
	font-family: "Montserrat", sans-serif;
	transition: all 0.3s;
	border-bottom: 2px solid #bebed2;
  }
  .form input:focus {
	border-bottom: 2px solid #78788c;
  }
  .form p:before {
	content: attr(type);
	display: block;
	margin: 28px 0 0;
	font-size: 14px;
  } 
 .send-message{
	float: right;
	padding: 8px 12px;
	margin: 8px 0 0;
	font-family: "Montserrat", sans-serif;
	border: 4px solid #78788c;
	border-radius: 10px;
	cursor: pointer;
	background-color: aquamarine;
	transition: all 0.3s;
  }
  .send-message:hover {
	background-color: rgb(0, 200, 0);
	color: white;
  }
.contact-me {
	position: absolute;
	bottom: -20px;
	background: #50505a;
	width: 300px;
	padding: 16px 4px 16px 0;
	border-radius: 6px;
	font-size: 13px;
	box-shadow: 10px 10px 40px -14px #000;
  }
  .contact-me span {
	margin: 0 5px 0 10px;
  }
  


  @media only screen and (max-width:600px) {
	.form {
		width: 50%;
		height: 440px;
	  }
	  .form input{
		font-size: 12px;
	  }
  }
  @media only screen and (max-width:414px) {
	.form {
		width: 60% ;
		height: 440px;
		margin: 215px 20%;
	  }
	 
  }