*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Poppins, sans-serif;
}

footer{
    .container-footer{
        background-color: black;
        color: white;
        height: 100%;
        width: 100%;
        padding: 10px;
        a{
            text-decoration: none;
            color: white;
            cursor: auto;
            width: fit-content !important;
        }
        .info-footer{
           
            display: grid;
            grid-template-columns: auto auto auto auto;
            justify-content: space-evenly;
            padding-left: 100PX;
            position: relative;
            
            .information-text{
                width: fit-content !important;
                padding: 20px;
                margin: 20px;
                line-height: 40px;
                display: flex;
                flex-direction: column;
                h3{
                    font-weight: 800;
                    font-size: 22px;
                }
                span{
                    font-size: 18px;
                    cursor: pointer;
                }
                a:hover{
                    color: yellowgreen;
                }
                button{
                    border: none ;
                    background-color: transparent;
                    font-size: 18px;
                    color: white;
                }
                .social-media{
                    line-height: 60px;
                    width: 300px;
                    span{
                        font-size: 20px;
                        font-weight: 800;
                    }
                    i{
                        font-size: 20px;
                        border: 1px solid white;
                        border-radius: 150px;
                        width: 40px;
                        height: 40px;
                        padding: 10px;
                        margin: 10px;
                    }
                }
            }
        }
        hr{
            color: white;
            width: 100%;
        }
        .footer-verification{
            display: flex;
            line-height: 30px;
            justify-content: center;
            p{
                padding: 20px;
                text-align: center;
            }
        }
    }
}
@media only screen and (max-width: 1280px){
    footer{
        .container-footer{
            .info-footer{
                
                .information-text{
                    width: 100%;
                    padding: 5px;
                    margin: 8px;
                    line-height: 40px;
                    a {
                        width: 210px;
                    }
                    .social-media {
                        line-height: 60px;
                        width: 255px;
                    }
                }
            }
        }
    }
}
